.category-container {
  width: 500px;
  height: 80vh;
}

.categories-container {
  display: flex;
  flex-direction: row;
  margin-left: 20%;
}

.categories-container .category-container {
  margin: 3rem;

}

.category-body-container h2 p {
  display: flex;
  flex-direction: row;

}

.category-body-container h2 {
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
}

.category-body-container p {
  font-size: 1.5rem;
  font-weight: 400;
  text-align: center;
}

.background-image {
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  margin: 20px;

}

@media only screen and (max-width: 600px) {
  .category-container {
    width: 400px;
    height: 300px;
  }

  .categories-container {
    display: flex;
    flex-direction: column;
    margin: 0%;
    padding: 0%;
  }

  .category-body-container h2 p {
    display: flex;
    flex-direction: column;

  }

  .category-body-container h2 {
    font-size: 2rem;
    font-weight: 700;
    text-align: center;
    margin-bottom: 0%;

  }

  .category-body-container p {
    font-size: 1.5rem;
    font-weight: 400;
    text-align: center;
    margin-bottom: 0%;
  }

  .background-image {
    width: 65%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    margin: auto;

  }
}